/*jshint esversion: 6 */

import Vue from 'vue';
import Vuex from 'vuex';
import { filters, STORAGE_KEY, todoStorage } from "../js/util.js";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        todos: todoStorage.fetch(),
        timeline: {},
        visibility: 'all'
    },

    getters: {
        remaining(state) {
            return filters.active(state.todos).length;
        },
        filteredTodos(state) {
            return filters[state.visibility](state.todos);
        }
    },

    mutations: {
        addTodo(state, payload) {
            let todos = {
                id: todoStorage.uid++,
                title: payload.title,
                completed: false,
                date: payload.date
            };

            state.todos.push(todos);

            if (!state.timeline[payload.date]) {
                state.timeline[payload.date] = {
                    todos: []
                };
            }

            state.timeline[payload.date].todos.push(todos);
        },

        removeTodo(state, todo) {
            state.todos.splice(state.todos.indexOf(todo), 1);
        },

        setAllDone(state, value) {
            state.todos.forEach(todo => {
                todo.completed = value;
            });
        },

        removeCompleted(state) {
            state.todos = filters.active(state.todos);
        },

        setVisibility(state, value) {
            if (value) {
                state.visibility = filters[value] ? value : 'all';
            }
        }
    }
});


